import React from 'react';
import './style.scss'

const listPages = [
    "Dashboard",
    "Customers",
    "Remittance",
    "P2P",
    "Transaction-history",
    "Todos",
]

function SideBar() {
    return (
        <div className={"side-bar"}>
            {listPages.map(page => {
                return <a key={page} href={page.toLowerCase()}>{page}</a>
            })}
        </div>
    );
}

export default SideBar;
