import React, {useState} from 'react';
import './style.scss'
import todo from "./Todos-store";
import {observer} from "mobx-react-lite";

const Todos = observer(() => {
    const [textTask, setTextTask] = useState<string>("");

    const handleSaveTask = (): void => {
        todo.addTodo(
            {
                completed: false,
                text: textTask,
                id: todo.todos[todo.todos.length - 1].id + 1
            });
        setTextTask("")
    }

    return <div className={"todos"}>
        <input type="text" value={textTask} onChange={(e) => setTextTask(e.target.value)}/>
        <button onClick={handleSaveTask}
        >create task
        </button>
        {todo.todos.map((t, ind) => {
            return <div key={t.id}>
                <input type="checkbox" defaultChecked={t.complete} onChange={() => todo.completedTodo(ind)}/>
                <p>{t.text}</p>
                <button onClick={() => todo.removeTodo(t.id)}>delete</button>
            </div>
        })}
    </div>
})

export default Todos;
